import React, { useState, useEffect } from "react";
import "../../App.css";
import { api } from "../../api/api";
export const Header = ({ recommendation_data, setrecommendation_data }) => {
  const searchHandler = (e) => {
    setsearchBox(e.target.value);
  };
  const [searchBox, setsearchBox] = useState("");

  const searchApi = () => {
    let url = "/recommendation/search/?page_size=50&search=" + searchBox;
    api
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        if (data) {
          if (data.results.length > 0) {
            setrecommendation_data(data.results);
          }
        }
      });
  };

  return (
    <header
      className="App-header"
      style={{
        minHeight: "50px",
        maxHeight: "60px",
        display: "flex",
        flexDirection: "row",
      }}
    >
      <div className="header-items">
        <p>WtW</p>
        <div className="search-container">
          <input
            className="search-box"
            type="text"
            placeholder="Search ..."
            onChange={(e) => {
              searchHandler(e);
            }}
          ></input>
          <button
            className="search-btn"
            onClick={() => {
              searchApi();
            }}
          >
            Search
          </button>
        </div>
        <button className="login-btn">Learn More</button>
      </div>
    </header>
  );
};
