import PropTypes from "prop-types";
import React, { useEffect, useState } from "react";
import { api } from "../api/api";
import { Card } from "../components/movie/card.jsx";
import { useLocation } from "react-router-dom";

export const HomeScreen = ({
  currentOtt,
  setcurrentOtt,
  recommendation_data,
  setrecommendation_data,
}) => {
  const useQuery = () => new URLSearchParams(useLocation().search);
  let query = useQuery();

  useEffect(() => {
    let pid = query.get("profile_id");
    if (pid === null) {
      pid = "3a0d184c08f8485d9fcdd0e2ced4bb58";
    }
    let url = "/recommendation/recommend/?user_id=" + pid;
    console.log(pid);
    api
      .get(url)
      .then((response) => response.data)
      .then((data) => {
        if (data) {
          console.log(data.length);
          setrecommendation_data(data);
        } else {
          data = [];
        }
      });
    return () => {};
  }, []);
  return (
    <div
      className="recommendation_row"
      style={{
        display: "flex",
        columnGap: "10px",
        flexWrap: "wrap",
        margin: "20px",
        justifyContent: "space-evenly",
        rowGap: "10px",
      }}
    >
      {recommendation_data.length >= 0 &&
        recommendation_data.map((data) => {
          return (
            <Card
              key={data.tmdb_id}
              currentOtt={currentOtt}
              setcurrentOtt={setcurrentOtt}
              image={data.poster_path}
              tmdb_id={data.tmdb_id}
              imdb_id={data.imdb_id}
              title={data.title}
              release_date={data.release_date}
              description={data.overview}
              vote_average={data.vote_average}
              backdrop={data.backdrop_path}
            />
          );
        })}
    </div>
  );
};
