import logo from "./static/logo.png";
import "./App.css";
import React, { useState } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import ReactDOM from "react-dom/client";
import "./index.css";
import { HomeScreen } from "./screens/homescreen";
import { DetailPage } from "./screens/detailPage";
import { Header } from "./components/header/header";
function App() {
  const [CurrentOtt, setCurrentOtt] = useState({});
  const [recommendation_data, setrecommendation_data] = useState([{}]);

  return (
    <div className="App">
      <BrowserRouter>
        <Header
          recommendation_data={recommendation_data}
          setrecommendation_data={setrecommendation_data}
        />
        <Routes>
          <Route
            path="/what_to_watch/"
            element={
              <HomeScreen
                currentOtt={CurrentOtt}
                setcurrentOtt={setCurrentOtt}
                recommendation_data={recommendation_data}
                setrecommendation_data={setrecommendation_data}
              />
            }
          />
          <Route
            path="/what_to_watch/detail/"
            element={
              <DetailPage
                currentOtt={CurrentOtt}
                setcurrentOtt={setCurrentOtt}
                recommendation_data={recommendation_data}
                setrecommendation_data={setrecommendation_data}
              />
            }
          />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
